// lib/api-client.ts
export type ApiOptions = RequestInit & { body?: any };

const API_BASE = process.env.NEXT_PUBLIC_API_URL || 'http://localhost:4000/api';

async function handleResp(res: Response) {
  const text = await res.text();
  let data;
  try { data = text ? JSON.parse(text) : undefined; } catch { data = text; }
  if (!res.ok) {
    const err: any = new Error(data?.message || res.statusText || 'API error');
    err.status = res.status;
    err.body = data;
    throw err;
  }
  return data;
}

export default async function apiClient(path: string, opts: ApiOptions = {}) {
  const url = path.startsWith('http') ? path : `${API_BASE}${path.startsWith('/') ? '' : '/'}${path}`;

  function getAuthHeader(): Record<string,string> {
    try {
      if (typeof window === 'undefined') return {};
      const token = (localStorage.getItem('token') || '').trim();
      return token ? { Authorization: `Bearer ${token}` } : {};
    } catch (e) {
      return {};
    }
  }

  const headers: Record<string,string> = {
    'Accept': 'application/json',
    ...getAuthHeader(),
    ...(opts.headers as Record<string,string> || {}),
  };

  let body: BodyInit | undefined;
  if (opts.body !== undefined) {
    headers['Content-Type'] = 'application/json';
    body = JSON.stringify(opts.body);
  }

  try {
    const res = await fetch(url, {
      credentials: 'include',
      ...opts,
      headers,
      body,
    });
    return await handleResp(res);
  } catch (err: any) {
    // If the error already comes from `handleResp` it may contain HTTP error details
    // (status, body). Preserve those properties when rethrowing so callers can
    // inspect `err.status` / `err.body` instead of losing them behind a wrapper.
    if (err && (err as any).body !== undefined) {
      // ensure url is available on the propagated error
      try { (err as any).url = url; } catch (_) {}
      throw err;
    }

    // Network-level error (CORS, DNS, connection refused, etc)
    const e: any = new Error(err?.message || 'Network error');
    e.detail = err;
    e.url = url;
    throw e;
  }
}
